import React from 'react';
import './App.css';
import AllStocksScreener from './AllStocksScreener';
import AllSectorScreener from './AllSectorScreener';




import { AiOutlineFundView } from 'react-icons/ai'

class Screener extends React.Component {
	  constructor(props) {
	    super(props);
	    this.state={isShowScreener:false};
	  }



	escFunction=(event)=>{
	    if(event.keyCode === 27) {
	    	this.showScreener(false);
	    }
	}



	componentDidMount() {
	  	document.addEventListener("keyup", this.escFunction, false);
	}

	componentWillUnmount(){
		document.removeEventListener("keyup", this.escFunction, false);
	}



	showScreener=(isShowScreener)=>{

		this.setState({isShowScreener:isShowScreener});
	}




  render() {

    const{
      kitedata,
      onTableRowclick,
      showToast,
      addToFavList
    }=this.props;



    const{
      isShowScreener,
      isShowAllStockScreener,
      isShowAllSectorScreener
    }=this.state;




    return (
    	<div className="screener">
	    	<div className="dropdown">
	          <button className="dropbtn"><AiOutlineFundView className="appicon"/></button>
	          <div className="dropdown-content screenerList">

		           <a onClick={(evt)=>{this.setState({isShowScreener:true,isShowAllStockScreener:true,isShowAllSectorScreener:false})}}>All Stocks</a>
		           <a onClick={(evt)=>{this.setState({isShowScreener:true,isShowAllSectorScreener:true,isShowAllStockScreener:false})}}>All Sectors</a>


	          </div>
	        </div>


			{ isShowScreener == true &&        
			      <div className="modal">       
			        <div className="modal-content">
			        <span onClick={(evt)=>{this.showScreener(false)}} className="close">&times;</span>

					    <div className='screener-dialog'>

					    { isShowAllStockScreener &&	<AllStocksScreener addToFavList={addToFavList} kitedata={kitedata} onTableRowclick={onTableRowclick}   />   }	 


					    { isShowAllSectorScreener && <AllSectorScreener addToFavList={addToFavList} kitedata={kitedata} onTableRowclick={onTableRowclick}   />   }	 



				        </div>

			    	</div>
			     </div>

			}     


		</div>


    )
  }



}


export default Screener;

